import React, { useEffect, useRef, useState } from "react";
import "./Timeline.scss";
import { LightText } from "../../CommonStyles/styles";
import health from "../../../assets/img/75hlogo.jpg";
import dblue from "../../../assets/img/dblue.gif";
import tek from "../../../assets/img/tek.png";

const Timeline = () => {
  return (
    <div class="timeline">
      <div class="dataContainer left">
        <div class="content">
          <h5>
            <img src={health} width={100} />
          </h5>
          <h5>Kaaspro Enterprises</h5>
          <LightText>FullStack Developer</LightText>
          <small> 2017 Aug - 2018 Sep</small>
        </div>
      </div>
      <div class="dataContainer right">
        <div class="content">
          <h5>
            <img src={dblue} width={60} height={50} />
          </h5>
          <h5>Doodleblue Innovations</h5>
          <LightText>Frontend Developer</LightText> <small> 2018 Sep - 2019 Aug</small>
        </div>
      </div>
      <div class="dataContainer left">
        <div class="content">
          <h5>
            <img src={tek} width={100} />
          </h5>
          <h5>Teknuance Info Solutions</h5>
          <LightText>Frontend Developer</LightText> <small> 2019 Aug - Present</small>
        </div>
      </div>
    </div>
  );
};

export default Timeline;
