import React from "react";
import { Heading, LightText } from "../CommonStyles/styles";
import SkillList from "./SkillList.js";

const Skills = () => {
  return (
    <div id="skills" className="text-center bg-light shadow-sm">
      <Heading size={2} className="f1">
        Skills
      </Heading>
      <div className="row f1">
        <div className="col-lg-4 border-right">
          <blockquote class="blockquote">
            <p class="mb-0">
              " My primary skill is the ingenuity in creating sustainable software products ,
              reviewing the UI erogonomics through enduser's perspective "
            </p>
            <br />
          </blockquote>
        </div>
        <div className="col-lg-8 col-xs-12">
          <SkillList />
        </div>
      </div>
    </div>
  );
};

export default Skills;
